<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.keyword" @keyup.enter.native="getRoles" v-on:blur="getRoles" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getRoles"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="roles" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="" sortable>
            </el-table-column>
            <el-table-column prop="updated_at" label="" sortable>
            </el-table-column>
            <el-table-column label="" width="200">
                <template scope="scope">
                    <el-button type="success" size="small" @click="handleShow(scope.$index, scope.row)"></el-button>
                    <el-button type="warning" size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="15" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!---->
        <el-dialog title="" v-model="addRoleVisible" :close-on-click-modal="false">
            <el-form :model="addRole" label-width="80px" :rules="addRoleRules" ref="addRole">
                <el-form-item label="" prop="name">
                    <el-input v-model="addRole.name " auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="permission">
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleAddPermissionAllChange"></el-checkbox>
                    <el-checkbox-group v-model="addRole.permissions" @change="handleAddPermissionChange">
                        <el-checkbox v-for="item in allPermissions" :label="item.name" :key="item.id" class="permission">{{item.cname}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addRoleVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" v-model="editRoleVisible" :close-on-click-modal="false">
            <el-form :model="editRole" label-width="80px" :rules="editRoleRules" ref="editRole">
                <el-form-item label="id" prop="id" hidden>
                    <el-input v-model="editRole.id" auto-complete="off" hidden></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editRole.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="permission">
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleEditPermissionAllChange"></el-checkbox>
                    <el-checkbox-group v-model="editRole.permissions" @change="handleEditPermissionChange">
                        <el-checkbox v-for="item in allPermissions" :label="item.name" :key="item.id" class="permission">{{item.cname}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editRoleVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
        <el-dialog title="" v-model="showRoleVisible" :close-on-click-model="false">
            <div class="dialog-body">
                <table class="table">
                    <tr>
                        <td>:</td>
                        <td>{{ showRole.name }}</td>
                    </tr>
                    <tr>
                        <td>:</td>
                        <td>{{ showRole.status }}</td>
                    </tr>
                    <tr>
                        <td>:</td>
                        <td>{{ showRole.description }}</td>
                    </tr>
                    <tr>
                        <td>:</td>
                        <td>{{ showRole.updated_at }}</td>
                    </tr>
                    <tr>
                        <td>:</td>
                        <td>{{ showRole.created_at }}</td>
                    </tr>
                </table>


            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="showRoleVisible = false"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    import {
        getRoleListPage,
        addRole,
        editRole,
        removeRole,
        batchRemoveRole,
        checkRoleNameApi,
        getAllPermissions,
        errors
    } from '../api/api';
    export default {
        data() {
            var checkAddName = (rule, value, callback) => {
                let para = {name: this.addRole.name}
                checkRoleNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            var checkEditName = (rule, value, callback) => {
                let para = {
                    id: this.editRole.id,
                    name: this.editRole.name
                }
                checkRoleNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            return {
                checkAll: false,
                allPermissions: [],
                isIndeterminate: true,
                cates: [],
                filters: {
                    keyword: ''
                },
                roles: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//
                showRoleVisible: false,//
                editRoleVisible: false, //
                editLoading: false,
                addRoleVisible: false,//
                addLoading: false,
                //
                showRole: {
                    id: '',
                    name: '',
                    guard_name: '',
                    description: '',
                    status: '',
                },
                //
                editRole: {
                    id: '',
                    name: '',
                    guard_name: '',
                    permissions: [],
                    status: '',
                },
                //
                addRole: {
                    name: '',
                    guard_name: '',
                    permissions: [],
                    status: '',
                },
                addRoleRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkAddName, trigger: 'blur'},
                    ],
                },
                editRoleRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkEditName, trigger: 'blur'}
                    ],
                },

            }

        },
        methods: {
            handleAddPermissionAllChange(event) {
                this.addRole.permissions = event.target.checked ? this.allPermissions.map(item => item.name) : [];
                this.isIndeterminate = false;
            },
            handleEditPermissionAllChange(event) {
                this.editRole.permissions = event.target.checked ? this.allPermissions.map(item => item.name) : [];
                this.isIndeterminate = false;
            },
            handleAddPermissionChange(value) {
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.addRole.permissions.length;
                this.isIndeterminate = checkedCount > 0 && checkedCount < this.addRole.permissions.length;
            },
            handleEditPermissionChange(value) {
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.editRole.permissions.length;
                this.isIndeterminate = checkedCount > 0 && checkedCount < this.editRole.permissions.length;
            },
            //
            formatStatus: function (row, column) {
                return row.status == '1' ? '' : row.status = '0' ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getRoles();
            },
            //
            getRoles() {
                let para = {
                    page: this.page,
                    keyword: this.filters.keyword
                };
                let _this = this;
                this.listLoading = true;
                getRoleListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.roles = res.data.data;
                    this.listLoading = false;
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleShow: function (index, row) {
                this.showRoleVisible = true;
                this.showRole = Object.assign({}, row);
                this.showRole.status = this.formatStatus(row);
            },
            //
            handleDel: function (index, row) {
                let _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {id: row.id};
                    removeRole(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getRoles();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleEdit: function (index, row) {
                getAllPermissions().then((res) => {
                    this.allPermissions = res.data
                });
                this.editRoleVisible = true;
                this.editRole = Object.assign({}, row);
                this.editRole.permissions = row.permissions.map(item => item.name)
            },
            //
            handleAdd: function () {
                this.addRoleVisible = true;
                getAllPermissions().then((res) => {
                    this.allPermissions = res.data
                });
                this.addRole = {
                    name: '',
                    gurdname: '',
                    permissions: [],
                    status: 1,
                };
            },
            //
            editSubmit: function () {
                this.$refs.editRole.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.editLoading = true;
                            let para = Object.assign({}, this.editRole);
                            editRole(para).then((res) => {
                                this.editLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editRole'].resetFields();
                                this.editRoleVisible = false;
                                this.getRoles();
                            }).catch((rs) => {
                                _this.editLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addRole.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.addLoading = true;
                            let para = Object.assign({}, this.addRole);
                            addRole(para).then((res) => {
                                this.addLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addRole'].resetFields();
                                this.addRoleVisible = false;
                                this.getRoles();
                            }).catch((rs) => {
                                _this.addLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let _this = this;
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {ids: ids};
                    batchRemoveRole(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: res.data.message,
                            type: 'success'
                        });
                        this.getRoles();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            }
        },
        mounted() {
            this.getRoles();
        }
    }

</script>

<style scoped>
    .table {
        border: 1px solid #99a9bf;
        line-height: 2;
        width: 100%;
    }

    .permission {
        margin-left: 0; margin-right: 10px;
    }

    table > tr > td { border: 1px solid #99a9bf; padding-left: 10px; text-align: left }
</style>


