<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.keyword" @keyup.enter.native="getDepartments" v-on:blur="getDepartments" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getDepartments"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" v-show="auth.view_department"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="departments" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="" sortable>
            </el-table-column>
            <el-table-column prop="hospital.name" label="" sortable>
            </el-table-column>
            <el-table-column prop="author" label="" sortable>
            </el-table-column>
            <el-table-column prop="status" label="" width="120" :formatter="formatStatus" sortable>
            </el-table-column>
            <el-table-column prop="updated_at" label="" sortable>
            </el-table-column>
            <el-table-column label="" width="200">
                <template scope="scope">
                    <el-button type="success" size="small"
                               @click="handleShow(scope.$index, scope.row)" v-show="auth.view_department">
                    </el-button>
                    <el-button type="warning" size="small"
                               @click="handleEdit(scope.$index, scope.row)" v-show="auth.edit_department">
                    </el-button>
                    <el-button type="danger" size="small"
                               @click="handleDel(scope.$index, scope.row)" v-show="auth.delete_department">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" v-show="auth.delete_department"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="15" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!---->
        <el-dialog title="" v-model="addDepartmentVisible" :close-on-click-modal="false">
            <el-form :model="addDepartment" label-width="80px" :rules="addDepartmentRules" ref="addDepartment">
                <el-form-item label="" prop="name">
                    <el-input v-model="addDepartment.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="hospital_id">
                    <el-select v-model="addDepartment.hospital_id" placeholder="">
                        <el-option :label="hospital.name" :value="hospital.id" v-for="hospital in hospitals" :key="hospital.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="addDepartment.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="addDepartment.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addDepartmentVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" v-model="editDepartmentVisible" :close-on-click-modal="false">
            <el-form :model="editDepartment" label-width="80px" :rules="editDepartmentRules" ref="editDepartment">
                <el-form-item label="id" prop="id" hidden>
                    <el-input v-model="editDepartment.id" auto-complete="off" hidden></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editDepartment.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="hospital_id">
                    <el-select v-model="editDepartment.hospital_id" placeholder="">
                        <el-option :label="hospital.name" :value="hospital.id" v-for="hospital in hospitals" :key="hospital.id"></el-option>

                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="editDepartment.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editDepartment.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editDepartmentVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
        <el-dialog title="" v-model="showDepartmentVisible" :close-on-click-model="false">
            <div class="dialog-body">
                <el-card class="box-card">
                    <div class="text item">:{{ showDepartment.name }}</div>
                    <div class="text item">:{{ showDepartment.hospital.name }}</div>
                    <div class="text item">:{{ showDepartment.status }}</div>
                    <div class="text item">:{{ showDepartment.description }}</div>
                    <div class="text item">:{{ showDepartment.created_at }}</div>
                    <div class="text item">:{{ showDepartment.updated_at }}</div>
                </el-card>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="showDepartmentVisible = false"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    import {
        getDepartmentListPage,
        getHospitalList,
        addDepartment,
        editDepartment,
        removeDepartment,
        batchRemoveDepartment,
        checkDepartmentNameApi,
        errors
    } from '../api/api';
    export default {
        data() {
            var checkAddName = (rule, value, callback) => {
                let para = {name: this.addDepartment.name}
                checkDepartmentNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            var checkEditName = (rule, value, callback) => {
                let para = {
                    id: this.editDepartment.id,
                    name: this.editDepartment.name
                }
                checkDepartmentNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            return {
                auth: {
                    view_department: false,
                    add_department: false,
                    edit_department: false,
                    delete_department: false
                },
                hospitals: [],
                filters: {
                    keyword: ''
                },
                departments: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//
                showDepartmentVisible: false,//
                editDepartmentVisible: false, //
                editLoading: false,
                //
                showDepartment: {
                    id: '',
                    name: '',
                    hospital: '',
                    description: '',
                    status: '',
                },
                //
                editDepartment: {
                    id: '',
                    name: '',
                    hospital_id: '',
                    description: '',
                    status: '',
                },
                //
                addDepartment: {
                    name: '',
                    hospital_id: '',
                    description: '',
                    status: '',
                },
                editDepartmentRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkEditName, trigger: 'blur'}
                    ],
                },
                addDepartmentVisible: false,//
                addLoading: false,
                addDepartmentRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkAddName, trigger: 'blur'},
                    ],
                },

            }

        },
        methods: {
            //
            formatStatus: function (row, column) {
                return row.status == '1' ? '' : row.status = '0' ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getDepartments();
            },
            //
            getDepartments() {
                let para = {
                    page: this.page,
                    keyword: this.filters.keyword
                };
                let _this = this;
                this.listLoading = true;
                getDepartmentListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.departments = res.data.data;
                    console.log(this.departments)
                    this.listLoading = false;
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleShow: function (index, row) {
                this.showDepartmentVisible = true;
                this.showDepartment = Object.assign({}, row);
                this.showDepartment.status = this.formatStatus(row);
            },
            //
            handleDel: function (index, row) {
                let _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {id: row.id};
                    removeDepartment(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getDepartments();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleEdit: function (index, row) {
                this.hospitals = [];
                getHospitalList().then((res) => {
                    this.hospitals = this.hospitals.concat(res.data);
                });
                this.editDepartmentVisible = true;
                this.editDepartment = Object.assign({}, row);

            },
            //
            handleAdd: function () {
                this.hospitals = []
                getHospitalList().then((res) => {
                    this.hospitals = this.hospitals.concat(res.data);
                });
                this.addDepartmentVisible = true;
                this.addDepartment = {
                    name: '',
                    hospital_id: '',
                    keyword: '',
                    description: '',
                    status: 1,
                };
            },
            //
            editSubmit: function () {
                this.$refs.editDepartment.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.editLoading = true;
                            let para = Object.assign({}, this.editDepartment);
                            editDepartment(para).then((res) => {
                                console.log(res)
                                this.editLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editDepartment'].resetFields();
                                this.editDepartmentVisible = false;
                                this.getDepartments();
                            }).catch((rs) => {
                                _this.editLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addDepartment.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.addLoading = true;
                            let para = Object.assign({}, this.addDepartment);
                            console.log(para);
                            addDepartment(para).then((res) => {
                                console.log(res)
                                this.addLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addDepartment'].resetFields();
                                this.addDepartmentVisible = false;
                                this.getDepartments();
                            }).catch((rs) => {
                                _this.addLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let _this = this;
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {ids: ids};
                    batchRemoveDepartment(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getDepartments();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            checkAuth(){
                let auth = JSON.parse(sessionStorage.getItem('auth'));
                auth.map((rs) => {
                    this.auth.hasOwnProperty(rs.name) ? this.auth[rs.name] = true : '';
                })
            }
        },
        mounted() {
            this.checkAuth();
            this.getDepartments();
        }
    }

</script>

<style scoped>
    .table {
        border: 1px solid #99a9bf;
        line-height: 2;
        width: 100%;
    }

    table > tr > td { border: 1px solid #99a9bf; padding-left: 10px; text-align: left }
</style>


