<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.keyword" @keyup.enter.native="getPatients" v-on:blur="getPatients" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getPatients"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" v-show="auth.view_patient"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleTable"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;" v-show="tableColumns">
            <el-form inline>
                <el-form-item>
                    <el-checkbox-group v-model="tableColumnsChecked">
                        <el-checkbox :label="index" v-for="(item,index) in cols">{{item.label}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="saveTableColumns"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="patients" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
                  border :default-expand-all="false">
            <el-table-column width="30" type="expand" label="">
                <template scope="props">
                    <el-form label-position="left" class="table-expand" label-width="40px">
                        <el-form-item label="" style="margin-bottom: 5px;">
                            <ul>
                                <li v-for="item in props.row.patient_content">
                                    {{item.content}}
                                </li>
                            </ul>
                        </el-form-item>
                        <el-form-item label="" style="margin-bottom: 5px;">
                            <ul>
                                <li v-for="item in props.row.patient_callback">
                                    {{item}}
                                </li>
                            </ul>
                        </el-form-item>
                        <el-form-item label="" style="margin-bottom: 5px;">
                            <ul>
                                <li v-for="item in props.row.patient_remark">
                                    {{item}}
                                </li>
                            </ul>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column
                    v-for="col in newcols"
                    :prop="col.prop"
                    :label="col.label"
                    :sortable="true"
                    align="center"
                    :width="col.width"
                    :resizable="true">
            </el-table-column>
            <el-table-column label="" width="200" fixed="right">
                <template scope="scope">
                    <el-button type="success" size="small"
                               @click="handleShow(scope.$index, scope.row)"
                               v-show="auth.view_patient">
                    </el-button>
                    <el-button type="warning" size="small"
                               @click="handleEdit(scope.$index, scope.row)" v-show="auth.edit_patient">
                    </el-button>
                    <el-button type="danger" size="small"
                               @click="handleDel(scope.$index, scope.row)" v-show="auth.delete_patient">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" v-show="auth.delete_patient"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="15" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!---->
        <el-dialog title="" v-model="addPatientVisible" :close-on-click-modal="false">
            <el-form :model="addPatient" label-width="80px" :rules="addPatientRules" ref="addPatient">
                <el-tabs type="border-card">
                    <el-tab-pane label="">
                        <el-form-item label="" prop="name">
                            <el-input v-model="addPatient.name" auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="phone">
                            <el-input v-model="addPatient.phone" auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="age">
                            <el-input v-model="addPatient.age" auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="sex">
                            <el-radio-group v-model="addPatient.sex">
                                <el-radio label="0"></el-radio>
                                <el-radio label="1"></el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="" prop="content">
                            <el-input type="textarea" v-model="addPatient.content" placeholder=""
                                      :autosize="{ minRows: 4, maxRows: 4}"
                            ></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="chatlog">
                            <el-input type="textarea" v-model="addPatient.chatlog" placeholder=""
                                      :autosize="{ minRows: 4, maxRows: 4}"
                            ></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="">
                        <el-form-item label="" prop="deparment_id">
                            <el-select v-model="addPatient.department_id" placeholder="" @change="handleDepartment">
                                <el-option :label="department.name" :value="department.id" v-for="department in departments" :key="department.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="doctor_id">
                            <el-select v-model="addPatient.doctor_id" placeholder="">
                                <el-option :label="doctor.name" :value="doctor.id" v-for="doctor in doctors" :key="doctor.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="disease_id">
                            <el-select v-model="addPatient.disease_id" placeholder="">
                                <el-option :label="disease.name" :value="disease.id" v-for="disease in diseases" :key="disease.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="media_id">
                            <el-select v-model="addPatient.media_id" placeholder="">
                                <el-option :label="media.name" :value="media.id" v-for="media in medias" :key="media.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="order_time">
                            <el-date-picker
                                    @change ="logs"
                                    v-model="addPatient.order_time"
                                    align="left"
                                    type="datetime"
                                    format="yyyy-MM-dd HH:mm"
                                    placeholder=""
                                    :picker-options="pickerOptions">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="remark">
                            <el-input type="textarea" v-model="addPatient.remark" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
                        </el-form-item>
                    </el-tab-pane>

                </el-tabs>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addPatientVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
        <!---->
    </section>
</template>

<script>
    import {
        getPatientListPage,
        getPatientMedia,
        getPatientDepartment,
        getPatientDisease,
        getPatientDoctor,
        addPatient,
        editPatient,
        removePatient,
        batchRemovePatient,
        savePatientColumns,
        getUserInfo,
        errors
    } from '../api/api';
    export default {
        data() {

            return {
                auth: {
                    view_patient: false,
                    add_patient: false,
                    edit_patient: false,
                    delete_patient: false
                },
                defaultexpandall: true,
                tableColumns: false,
                tableColumnsChecked: [],
                newcols: [],
                cols: {
                    name: {prop: 'name', label: ''},
                    sex: {prop: 'sex', label: '',},
                    age: {prop: 'age', label: '',},
                    phone: {prop: 'phone', label: '', width: '140'},
                    order_time: {prop: 'order_time', label: '', width: '180'},
                    erea: {prop: 'erea', label: '',},
                    media: {prop: 'media.name', label: '',},
                    disease: {prop: 'disease.name', label: '',},
                    department: {prop: 'department.name', label: '',},
                    depart: {prop: 'depart.name', label: '',},
                    doctor: {prop: 'doctor.name', label: '',},
                    user: {prop: 'user.name', label: '',},
                    status: {prop: 'status', label: '',},
                    updated_at: {prop: 'updated_at', label: '', width: '180'},
                },
                departments: [],
                doctors: [],
                diseases: [],
                medias: [],
                filters: {
                    keyword: ''
                },
                patients: [],
                total: 0,
                page: 1,
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    },{
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    },{
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24*2);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                listLoading: false,
                sels: [],//
                showPatientVisible: false,//
                editPatientVisible: false, //
                editLoading: false,
                //
                showPatient: {
                    id: '',
                    name: '',
                    group: '',
                    description: '',
                    status: '',
                },
                //
                editPatient: {
                    id: '',
                    name: '',
                    group: '',
                    description: '',
                    status: '',
                },
                //
                addPatient: {
                    name: '',
                    sex: '',
                    age: '',
                    phone: '',
                    department_id: '',
                    doctor_id: '',
                    disease_id: '',
                    media_id: '',
                    order_time: '',
                    chatlog: '',
                    content: '',
                    remark: '',
                },
                editPatientRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],

                },
                addPatientVisible: false,//
                addLoading: false,
                addPatientRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                },
            }

        },
        computed: {
            hospital () {
                return this.$store.state.hospital
            }
        },
        watch: {
            hospital(){
                this.getPatients();
            }
        },
        methods: {
            logs(value){
                this.addPatient.order_time = value;
                console.log(this.addPatient.order_time)
            },
            handleTable(){
                this.tableColumns = !this.tableColumns;
            },
            saveTableColumns(){
                let para = {tableColumns: this.tableColumnsChecked}
                savePatientColumns(para).then((res) => {
                    if (res.status === 200) {
                        this.$router.go({name: 'patient'})
                    } else {
                        this.$message({
                            message: '',
                            type: 'error'
                        });
                    }

                });
            },
            changeTableColumns () {
                getUserInfo().then((res) => {
                    let defaltColumns = [];
                    for (let col in this.cols) {
                        defaltColumns.push(col)
                    }
                    let setColumns = JSON.parse(res.data.user_set.patient_column);
                    this.tableColumnsChecked = setColumns.length ? setColumns : defaltColumns;
                    let data = [];
                    for (let col in this.cols) {
                        if (this.tableColumnsChecked.indexOf(col) >= 0) {
                            data.push(this.cols[col])
                        }
                    }
                    this.newcols = data;
                })
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getPatients();
            },
            //
            getPatients() {
                let para = {
                    page: this.page,
                    keyword: this.filters.keyword,
                    hospital: this.hospital
                };
                console.log(para);
                let _this = this;
                this.listLoading = true;
                getPatientListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.patients = res.data.data;
                    this.listLoading = false;
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
                this.changeTableColumns();
            },
            //
            handleShow: function (index, row) {
                this.showPatientVisible = true;
                this.showPatient = Object.assign({}, row);
                this.showPatient.status = this.formatStatus(row);
            },
            //
            handleDel: function (index, row) {
                let _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {id: row.id};
                    removePatient(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getPatients();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleEdit: function (index, row) {
                this.groups = [];
                getPatientGroup().then((res) => {
                    this.groups = this.groups.concat(res.data);
                });
                this.editPatientVisible = true;
                this.editPatient = Object.assign({}, row);

            },

            //
            handleAdd: function () {
                if (this.hospital == '') {
                    this.$message({
                        message: '',
                        type: 'error'
                    });
                } else {
                    let para = {hospital: this.hospital}
                    getPatientMedia(para).then((res) => {
                        this.medias = [];
                        this.medias = this.medias.concat(res.data);
                    });
                    getPatientDepartment(para).then((res) => {
                        this.departments = [];
                        this.departments = this.departments.concat(res.data);
                    });

                    this.addPatientVisible = true;
                    this.addPatient = {
                        name: '',
                        sex: '',
                        age: '',
                        phone: '',
                        department_id: '',
                        doctor_id: '',
                        disease_id: '',
                        media_id: '',
                        order_time: '',
                        chatlog: '',
                        content: '',
                        remark: '',
                    };
                }
            },
            handleDepartment:function () {
                let para ={department_id:this.addPatient.department_id}
                console.log(this.addPatient.department_id)
                getPatientDoctor(para).then((res) => {
                    this.doctors = [];
                    this.doctors = this.doctors.concat(res.data);
                });
                getPatientDisease(para).then((res) => {
                    this.diseases = [];
                    this.diseases = this.diseases.concat(res.data);
                });
            },
            //
            editSubmit: function () {
                this.$refs.editPatient.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.editLoading = true;
                            let para = Object.assign({}, this.editPatient);
                            editPatient(para).then((res) => {
                                console.log(res)
                                this.editLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editPatient'].resetFields();
                                this.editPatientVisible = false;
                                this.getPatients();
                            }).catch((rs) => {
                                _this.editLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addPatient.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.addLoading = true;
                            let para = Object.assign({}, this.addPatient);
                            para.hospital_id = this.hospital;
                            addPatient(para).then((res) => {
                                console.log(res)
                                this.addLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addPatient'].resetFields();
                                this.addPatientVisible = false;
                                this.getPatients();
                            }).catch((rs) => {
                                _this.addLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let _this = this;
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {ids: ids};
                    batchRemovePatient(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getPatients();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            checkAuth(){
                let auth = JSON.parse(sessionStorage.getItem('auth'));
                auth.map((rs) => {
                    this.auth.hasOwnProperty(rs.name) ? this.auth[rs.name] = true : '';
                })
            }
        },
        mounted() {
            this.checkAuth();
            this.getPatients();
        }
    }

</script>

<style scoped>
    .text {
        font-size: 14px;
    }

    .line { text-align: center; }

    .item {
        padding: 5px 0;
    }

    .table-expand div { width: 30%; float: left; }

    .table-expand ul { line-height: 24px; float: left; padding: 5px; margin: 0; }

    .table-expand ul li { list-style: none }

    .cell { padding-left: 5px; padding-right: 5px; }

</style>


