<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.keyword" @keyup.enter.native="getMedias" v-on:blur="getMedias" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getMedias"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" v-show="auth.view_media"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="medias" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="" sortable>
            </el-table-column>
            <el-table-column prop="hospital.name" label="" sortable>
            </el-table-column>
            <el-table-column prop="author" label="" sortable>
            </el-table-column>
            <el-table-column prop="status" label="" width="120" :formatter="formatStatus" sortable>
            </el-table-column>
            <el-table-column prop="updated_at" label="" sortable>
            </el-table-column>
            <el-table-column label="" width="200">
                <template scope="scope">
                    <el-button type="success" size="small"
                               @click="handleShow(scope.$index, scope.row)" v-show="auth.view_media">
                    </el-button>
                    <el-button type="warning" size="small"
                               @click="handleEdit(scope.$index, scope.row)" v-show="auth.edit_media">
                    </el-button>
                    <el-button type="danger" size="small"
                               @click="handleDel(scope.$index, scope.row)" v-show="auth.delete_media">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" v-show="auth.delete_media"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="15" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!---->
        <el-dialog title="" v-model="addMediaVisible" :close-on-click-modal="false">
            <el-form :model="addMedia" label-width="80px" :rules="addMediaRules" ref="addMedia">
                <el-form-item label="" prop="name">
                    <el-input v-model="addMedia.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="hospital_id">
                    <el-select v-model="addMedia.hospital_id" placeholder="">
                        <el-option :label="hospital.name" :value="hospital.id" v-for="hospital in hospitals" :key="hospital.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="addMedia.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="addMedia.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addMediaVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" v-model="editMediaVisible" :close-on-click-modal="false">
            <el-form :model="editMedia" label-width="80px" :rules="editMediaRules" ref="editMedia">
                <el-form-item label="id" prop="id" hidden>
                    <el-input v-model="editMedia.id" auto-complete="off" hidden></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editMedia.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="hospital_id">
                    <el-select v-model="editMedia.hospital_id" placeholder="">
                        <el-option :label="hospital.name" :value="hospital.id" v-for="hospital in hospitals" :key="hospital.id"></el-option>

                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="editMedia.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editMedia.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editMediaVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
        <el-dialog title="" v-model="showMediaVisible" :close-on-click-model="false">
            <div class="dialog-body">
                <el-card class="box-card">
                    <div class="text item">:{{ showMedia.name }}</div>
                    <div class="text item">:{{ showMedia.hospital.name }}</div>
                    <div class="text item">:{{ showMedia.status }}</div>
                    <div class="text item">:{{ showMedia.description }}</div>
                    <div class="text item">:{{ showMedia.created_at }}</div>
                    <div class="text item">:{{ showMedia.updated_at }}</div>
                </el-card>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="showMediaVisible = false"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    import {
        getMediaListPage,
        getHospitalList,
        addMedia,
        editMedia,
        removeMedia,
        batchRemoveMedia,
        errors
    } from '../api/api';
    export default {
        data() {
            return {
                auth: {
                    view_media: false,
                    add_media: false,
                    edit_media: false,
                    delete_media: false
                },
                hospitals: [],
                filters: {
                    keyword: ''
                },
                medias: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//
                showMediaVisible: false,//
                editMediaVisible: false, //
                editLoading: false,
                //
                showMedia: {
                    id: '',
                    name: '',
                    hospital: '',
                    description: '',
                    status: '',
                },
                //
                editMedia: {
                    id: '',
                    name: '',
                    hospital_id: '',
                    description: '',
                    status: '',
                },
                //
                addMedia: {
                    name: '',
                    hospital_id: '',
                    description: '',
                    status: '',
                },
                editMediaRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                },
                addMediaVisible: false,//
                addLoading: false,
                addMediaRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                },

            }

        },
        methods: {
            //
            formatStatus: function (row, column) {
                return row.status == '1' ? '' : row.status = '0' ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getMedias();
            },
            //
            getMedias() {
                let para = {
                    page: this.page,
                    keyword: this.filters.keyword
                };
                let _this = this;
                this.listLoading = true;
                getMediaListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.medias = res.data.data;
                    console.log(this.medias)
                    this.listLoading = false;
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleShow: function (index, row) {
                this.showMediaVisible = true;
                this.showMedia = Object.assign({}, row);
                this.showMedia.status = this.formatStatus(row);
            },
            //
            handleDel: function (index, row) {
                let _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {id: row.id};
                    removeMedia(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getMedias();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleEdit: function (index, row) {
                this.hospitals = [];
                getHospitalList().then((res) => {
                    this.hospitals = this.hospitals.concat(res.data);
                });
                this.editMediaVisible = true;
                this.editMedia = Object.assign({}, row);

            },
            //
            handleAdd: function () {
                this.hospitals = []
                getHospitalList().then((res) => {
                    this.hospitals = this.hospitals.concat(res.data);
                });
                this.addMediaVisible = true;
                this.addMedia = {
                    name: '',
                    hospital_id: '',
                    keyword: '',
                    description: '',
                    status: 1,
                };
            },
            //
            editSubmit: function () {
                this.$refs.editMedia.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.editLoading = true;
                            let para = Object.assign({}, this.editMedia);
                            editMedia(para).then((res) => {
                                console.log(res)
                                this.editLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editMedia'].resetFields();
                                this.editMediaVisible = false;
                                this.getMedias();
                            }).catch((rs) => {
                                _this.editLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addMedia.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.addLoading = true;
                            let para = Object.assign({}, this.addMedia);
                            console.log(para);
                            addMedia(para).then((res) => {
                                console.log(res)
                                this.addLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addMedia'].resetFields();
                                this.addMediaVisible = false;
                                this.getMedias();
                            }).catch((rs) => {
                                _this.addLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let _this = this;
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {ids: ids};
                    batchRemoveMedia(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getMedias();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            checkAuth(){
                let auth = JSON.parse(sessionStorage.getItem('auth'));
                auth.map((rs) => {
                    this.auth.hasOwnProperty(rs.name) ? this.auth[rs.name] = true : '';
                })
            }
        },
        mounted() {
            this.checkAuth();
            this.getMedias();
        }
    }

</script>

<style scoped>
   
</style>


