import Login from "./views/Login.vue";
import NotFound from "./views/404.vue";
import Home from "./views/Home.vue";
import Main from "./views/Main.vue";
import Report from "./views/Report.vue";
import User from "./views/User.vue";
import Role from "./views/Role.vue";
import Permission from "./views/Permission.vue";
import Log from "./views/Log.vue";
import Hospital from "./views/Hospital.vue";
import Department from "./views/Department.vue";
import Disease from "./views/Disease.vue";
import Doctor from "./views/Doctor.vue";
import Media from "./views/Media.vue";
import Depart from "./views/Depart.vue";
import Patient from "./views/Patient.vue";

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        leaf: true,//
        iconCls: 'el-icon-menu',//class
        children: [
            {path: '/home', component: Main, name: '', hidden: false},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        leaf: true,//
        iconCls: 'fa fa-indent',
        children: [
            {path: '/patient', component: Patient, name: '',auth:'view_patient'},

        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-indent',
        children: [
            {path: '/hospital', component: Hospital, name: '',auth:'view_hospital'},
            {path: '/department', component: Department, name: '',auth:'view_department'},
            {path: '/doctor', component: Doctor, name: '',auth:'view_doctor'},
            {path: '/disease', component: Disease, name: '',auth:'view_disease'},
            {path: '/media', component: Media, name: '',auth:'view_media'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa el-icon-setting',
        children: [
            {path: '/depart', component: Depart, name: '',auth:'view_depart'},
            {path: '/user', component: User, name: '',auth:'view_user'},
            {path: '/role', component: Role, name: '',auth:'view_role'},
            {path: '/permission', component: Permission, name: '',auth:'view_permission'},
            {path: '/log', component: Log, name: '',auth:'view_log'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            {path: '/report', component: Report, name: '',auth:'view_report'},

        ]
    },

    {
        path: '*',
        hidden: true,
        redirect: {path: '/404'}
    }
];

export default routes;


