<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.keyword" @keyup.enter.native="getDeparts" v-on:blur="getDeparts" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getDeparts"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" v-show="auth.view_depart"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="departs" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="" sortable>
            </el-table-column>
            <el-table-column prop="pidName" label="" sortable>
            </el-table-column>
            <el-table-column prop="author" label="" sortable>
            </el-table-column>
            <el-table-column prop="status" label="" width="100" :formatter="formatStatus" sortable>
            </el-table-column>
            <el-table-column prop="updated_at" label="" sortable>
            </el-table-column>
            <el-table-column label="" width="200">
                <template scope="scope">
                    <el-button type="success" size="small"
                               @click="handleShow(scope.$index, scope.row)" v-show="auth.view_depart">
                    </el-button>
                    <el-button type="warning" size="small"
                               @click="handleEdit(scope.$index, scope.row)" v-show="auth.edit_depart">
                    </el-button>
                    <el-button type="danger" size="small"
                               @click="handleDel(scope.$index, scope.row)" v-show="auth.delete_depart">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" v-show="auth.delete_depart"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="15" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!---->
        <el-dialog title="" v-model="addDepartVisible" :close-on-click-modal="false">
            <el-form :model="addDepart" label-width="80px" :rules="addDepartRules" ref="addDepart">
                <el-form-item label="" prop="name">
                    <el-input v-model="addDepart.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pid">
                    <el-select v-model="addDepart.pid" placeholder="">
                        <el-option :label="parentdpart.name" :value="parentdpart.id" v-for="parentdpart in parentDeparts" :key="parentdpart.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="addDepart.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="addDepart.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addDepartVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" v-model="editDepartVisible" :close-on-click-modal="false">
            <el-form :model="editDepart" label-width="80px" :rules="editDepartRules" ref="editDepart">
                <el-form-item label="id" prop="id" hidden>
                    <el-input v-model="editDepart.id" auto-complete="off" hidden></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editDepart.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pid">
                    <el-select v-model="editDepart.pid" placeholder="">
                        <el-option :label="parentdpart.name" :value="parentdpart.id" v-for="parentdpart in parentDeparts" :key="parentdpart.id"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-input type="textarea" v-model="editDepart.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editDepart.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editDepartVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
        <el-dialog title="" v-model="showDepartVisible" :close-on-click-model="false">
            <div class="dialog-body">
                <el-card class="box-card">
                    <div class="text item">:{{ showDepart.name }}</div>
                    <div class="text item">:{{ showDepart.parentName }}</div>
                    <div class="text item">:{{ showDepart.status }}</div>
                    <div class="text item">:{{ showDepart.description }}</div>
                    <div class="text item">:{{ showDepart.created_at }}</div>
                    <div class="text item">:{{ showDepart.updated_at }}</div>
                </el-card>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="showDepartVisible = false"></el-button>
            </div>


        </el-dialog>
    </section>
</template>

<script>
    import {
        getDepartListPage,
        getDepartList,
        addDepart,
        editDepart,
        removeDepart,
        batchRemoveDepart,
        checkDepartNameApi,
        errors
    } from '../api/api';
    export default {
        data() {
            var checkAddName = (rule, value, callback) => {
                let para = {name: this.addDepart.name}
                checkDepartNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            var checkEditName = (rule, value, callback) => {
                let para = {
                    id: this.editDepart.id,
                    name: this.editDepart.name
                }
                checkDepartNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            return {
                auth: {
                    view_depart: false,
                    add_depart: false,
                    edit_depart: false,
                    delete_depart: false
                },
                parentDeparts: [],
                filters: {
                    keyword: ''
                },
                departs: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//
                showDepartVisible: false,//
                editDepartVisible: false, //
                editLoading: false,
                //
                showDepart: {
                    id: '',
                    name: '',
                    parentName: '',
                    description: '',
                    status: '',
                },
                //
                editDepart: {
                    id: '',
                    name: '',
                    pid: '',
                    description: '',
                    status: '',
                },
                //
                addDepart: {
                    name: '',
                    pid: '',
                    description: '',
                    status: '',
                },
                editDepartRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkEditName, trigger: 'blur'}
                    ],
                },
                addDepartVisible: false,//
                addLoading: false,
                addDepartRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkAddName, trigger: 'blur'},
                    ],
                },

            }

        },
        methods: {
            //
            formatStatus: function (row, column) {
                return row.status == '1' ? '' : row.status = '0' ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getDeparts();
            },
            //
            getDeparts() {
                let para = {
                    page: this.page,
                    keyword: this.filters.keyword
                };
                let _this = this;
                this.listLoading = true;
                getDepartListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.departs = res.data.data;
                    this.listLoading = false;
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleShow: function (index, row) {
                this.showDepartVisible = true;
                this.showDepart = Object.assign({}, row);
                this.showDepart.status = this.formatStatus(row);
            },
            //
            handleDel: function (index, row) {
                let _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {id: row.id};
                    removeDepart(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getDeparts();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleEdit: function (index, row) {
                this.parentDeparts = [{name: '', id: 0}];
                getDepartList().then((res) => {
                    this.parentDeparts = this.parentDeparts.concat(res.data);
                });

                this.editDepartVisible = true;
                this.editDepart = Object.assign({}, row);

            },
            //
            handleAdd: function () {
                this.parentDeparts = [{name: '', id: 0}];
                getDepartList().then((res) => {
                    console.log(res)
                    this.parentDeparts = this.parentDeparts.concat(res.data);
                });
                console.log(this.parentDeparts)
                this.addDepartVisible = true;
                this.addDepart = {
                    name: '',
                    pid: '',
                    description: '',
                    status: 1,
                };
            },
            //
            editSubmit: function () {
                this.$refs.editDepart.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.editLoading = true;
                            let para = Object.assign({}, this.editDepart);
                            editDepart(para).then((res) => {
                                this.editLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editDepart'].resetFields();
                                this.editDepartVisible = false;
                                this.getDeparts();
                            }).catch((rs) => {
                                _this.editLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addDepart.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.addLoading = true;
                            let para = Object.assign({}, this.addDepart);
                            addDepart(para).then((res) => {
                                console.log(res)
                                this.addLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addDepart'].resetFields();
                                this.addDepartVisible = false;
                                this.getDeparts();
                            }).catch((rs) => {
                                _this.addLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let _this = this;
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {ids: ids};
                    batchRemoveDepart(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getDeparts();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            checkAuth(){
                let auth = JSON.parse(sessionStorage.getItem('auth'));
                auth.map((rs) => {
                    this.auth.hasOwnProperty(rs.name) ? this.auth[rs.name] = true : '';
                })
            }
        },
        mounted() {
            this.checkAuth();
            this.getDeparts();
        }
    }

</script>

<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        padding: 5px 0;
    }
</style>


