<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.keyword" @keyup.enter.native="getHospitals" v-on:blur="getHospitals" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getHospitals"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" v-show="auth.view_hospital"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="hospitals" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="name" label="" sortable>
            </el-table-column>
            <el-table-column prop="group" label="" sortable>
            </el-table-column>
            <el-table-column prop="author" label="" sortable>
            </el-table-column>
            <el-table-column prop="status" label="" width="100" :formatter="formatStatus" sortable>
            </el-table-column>
            <el-table-column prop="updated_at" label="" sortable>
            </el-table-column>
            <el-table-column label="" width="200">
                <template scope="scope">
                    <el-button type="success" size="small"
                               @click="handleShow(scope.$index, scope.row)" v-show="auth.view_hospital">
                    </el-button>
                    <el-button type="warning" size="small"
                               @click="handleEdit(scope.$index, scope.row)" v-show="auth.edit_hospital">
                    </el-button>
                    <el-button type="danger" size="small"
                               @click="handleDel(scope.$index, scope.row)" v-show="auth.delete_hospital">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0" v-show="auth.delete_hospital"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="15" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!---->
        <el-dialog title="" v-model="addHospitalVisible" :close-on-click-modal="false">
            <el-form :model="addHospital" label-width="80px" :rules="addHospitalRules" ref="addHospital">
                <el-form-item label="" prop="name">
                    <el-input v-model="addHospital.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="group">
                    <el-select v-model="addHospital.group" placeholder="" filterable allow-create>
                        <el-option :label="group.name" :value="group.name" v-for="group in groups" :key="group.name"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="addHospital.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="addHospital.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addHospitalVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" v-model="editHospitalVisible" :close-on-click-modal="false">
            <el-form :model="editHospital" label-width="80px" :rules="editHospitalRules" ref="editHospital">
                <el-form-item label="id" prop="id" hidden>
                    <el-input v-model="editHospital.id" auto-complete="off" hidden></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editHospital.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="group">
                    <el-select v-model="editHospital.group" placeholder="">
                        <el-option :label="group.name" :value="group.name" v-for="group in groups" :key="group.name"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-input type="textarea" v-model="editHospital.description"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editHospital.status">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editHospitalVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
        <el-dialog title="" v-model="showHospitalVisible" :close-on-click-model="false">
            <div class="dialog-body">
                <el-card class="box-card">
                    <div class="text item">:{{ showHospital.name }}</div>
                    <div class="text item">:{{ showHospital.group }}</div>
                    <div class="text item">:{{ showHospital.status }}</div>
                    <div class="text item">:{{ showHospital.description }}</div>
                    <div class="text item">:{{ showHospital.created_at }}</div>
                    <div class="text item">:{{ showHospital.updated_at }}</div>
                </el-card>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="showHospitalVisible = false"></el-button>
            </div>


        </el-dialog>
    </section>
</template>

<script>
    import {
        getHospitalListPage,
        getHospitalGroup,
        addHospital,
        editHospital,
        removeHospital,
        batchRemoveHospital,
        checkHospitalNameApi,
        errors
    } from '../api/api';
    export default {
        data() {
            var checkAddName = (rule, value, callback) => {
                let para = {name: this.addHospital.name}
                checkHospitalNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            var checkEditName = (rule, value, callback) => {
                let para = {
                    id: this.editHospital.id,
                    name: this.editHospital.name
                }
                checkHospitalNameApi(para).then((res) => {
                    console.log(res.data.status)
                    if (!res.data.status) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                })
            };
            return {
                auth: {
                    view_hospital: false,
                    add_hospital: false,
                    edit_hospital: false,
                    delete_hospital: false
                },
                groups: [],
                filters: {
                    keyword: ''
                },
                hospitals: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//
                showHospitalVisible: false,//
                editHospitalVisible: false, //
                editLoading: false,
                //
                showHospital: {
                    id: '',
                    name: '',
                    group: '',
                    description: '',
                    status: '',
                },
                //
                editHospital: {
                    id: '',
                    name: '',
                    group: '',
                    description: '',
                    status: '',
                },
                //
                addHospital: {
                    name: '',
                    group: '',
                    description: '',
                    status: '',
                },
                editHospitalRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkEditName, trigger: 'blur'}
                    ],
                },
                addHospitalVisible: false,//
                addLoading: false,
                addHospitalRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkAddName, trigger: 'blur'},
                    ],
                },

            }

        },
        methods: {
            //
            formatStatus: function (row, column) {
                return row.status == '1' ? '' : row.status = '0' ? '' : '';
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getHospitals();
            },
            //
            getHospitals() {
                let para = {
                    page: this.page,
                    keyword: this.filters.keyword
                };
                let _this = this;
                this.listLoading = true;
                getHospitalListPage(para).then((res) => {
                    this.total = res.data.total;
                    this.hospitals = res.data.data;
                    this.listLoading = false;
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleShow: function (index, row) {
                this.showHospitalVisible = true;
                this.showHospital = Object.assign({}, row);
                this.showHospital.status = this.formatStatus(row);
            },
            //
            handleDel: function (index, row) {
                let _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {id: row.id};
                    removeHospital(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getHospitals();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            handleEdit: function (index, row) {
                this.groups = [];
                getHospitalGroup().then((res) => {
                    this.groups = this.groups.concat(res.data);
                });
                this.editHospitalVisible = true;
                this.editHospital = Object.assign({}, row);

            },
            //
            handleAdd: function () {
                getHospitalGroup().then((res) => {
                    this.groups = [];
                    this.groups = this.groups.concat(res.data);
                });
                this.addHospitalVisible = true;
                this.addHospital = {
                    name: '',
                    group: '',
                    description: '',
                    status: 1,
                };
            },
            //
            editSubmit: function () {
                this.$refs.editHospital.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.editLoading = true;
                            let para = Object.assign({}, this.editHospital);
                            editHospital(para).then((res) => {
                                console.log(res)
                                this.editLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editHospital'].resetFields();
                                this.editHospitalVisible = false;
                                this.getHospitals();
                            }).catch((rs) => {
                                _this.editLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addHospital.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let _this = this;
                            this.addLoading = true;
                            let para = Object.assign({}, this.addHospital);
                            addHospital(para).then((res) => {
                                console.log(res)
                                this.addLoading = false;
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addHospital'].resetFields();
                                this.addHospitalVisible = false;
                                this.getHospitals();
                            }).catch((rs) => {
                                _this.addLoading = false;
                                _this.$message({
                                    message: errors(rs).message,
                                    type: errors(rs).type,
                                    duration: errors(rs).duration
                                });
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let _this = this;
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = {ids: ids};
                    batchRemoveHospital(para).then((res) => {
                        this.listLoading = false;
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getHospitals();
                    });
                }).catch((rs) => {
                    _this.listLoading = false;
                    _this.$message({
                        message: errors(rs).message,
                        type: errors(rs).type,
                        duration: errors(rs).duration
                    });
                });
            },
            //
            checkAuth(){
                let auth = JSON.parse(sessionStorage.getItem('auth'));
                auth.map((rs) => {
                    this.auth.hasOwnProperty(rs.name) ? this.auth[rs.name] = true : '';
                })
            }
        },
        mounted() {
            this.checkAuth();
            this.getHospitals();
        }
    }

</script>

<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        padding: 5px 0;
    }
</style>


